import {motion} from "framer-motion";

const variants = {
    hidden: {
        opacity: 0,
    },
    visible: {
        opacity: 1,
        transition: {
            delay: 1,
            duration: 0.2
        }
    }
}

const AnimationChildrenComponent = ({children}) => {
    return (
        <motion.div
            variants={variants}
            initial='hidden'
            animate='visible'
        >
            {children}
        </motion.div>
    )
}

export default AnimationChildrenComponent;