import {motion} from "framer-motion";

const variants = {
    hidden: {
        opacity: 0,
        x: '50vw',
    },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            duration: 0.4,
            type: 'spring',
            mass: 0.35,
            damping: 5,
            stiffness: 200,
            delay: 0.7
        }
    },
    collapse: {
        opacity: 0,
        x: '60vw',
        transition: {
            duration: 0.2,
        }
    }
}

const AnimationSlideComponent = ({children}) => {
    return (
        <motion.div
            variants={variants}
            initial='hidden'
            animate='visible'
            exit='collapse'
        >
            {children}
        </motion.div>
    )
}

export default AnimationSlideComponent;