import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";

import PhotoCard from "../photo-card/photo-card.component";
import AnimationSlideComponent from '../../../effects/Animation.slide.component'
import AnimationChildrenComponent from "../../../effects/Animation.children.component";
import AnimationHoverCategoriesComponent from "../../../effects/Animation.hover.categories.component";

import {
    CategoryPreviewContainer,
    CategoryPreviewGrid,
    CategoryPreviewTitle,
} from './category-preview.styles'

const CategoryPreview = ({title, products}) => {
    const navigate = useNavigate();
    const [windowResolution, detectWR] = useState(window.innerWidth);
    const [condition, setCondition] = useState(4);

    const detectSize = () => {
        detectWR(window.innerWidth);
    };

    const conditionChecker = () => {
        if( windowResolution > 1280) {
            setCondition(4)
        }else if (windowResolution < 1280 && windowResolution > 768) {
            setCondition(3)
        } else if (windowResolution < 768 && windowResolution > 481) {
            setCondition(2)
        } else if ( windowResolution <= 481) {
            setCondition(1)
        }
    }

    useEffect(() => {
        window.addEventListener('resize', detectSize);
        conditionChecker();

        return() => window.removeEventListener('resize', detectSize)

    }, [windowResolution]);

    const titleClickHandler = () => {
        navigate(title)
    }

    return (
        <CategoryPreviewContainer>
            <AnimationSlideComponent>
                <CategoryPreviewTitle>
                    <AnimationHoverCategoriesComponent>
                        <span onClick={titleClickHandler}>{title.toUpperCase()}</span>
                    </AnimationHoverCategoriesComponent>
                </CategoryPreviewTitle>
            </AnimationSlideComponent>
            <AnimationChildrenComponent>
                <CategoryPreviewGrid>
                    {
                        products
                            .filter((_, idx) => idx < condition)
                            .map((product) =>
                                <PhotoCard key={product.id} merch={product}/>)
                    }
                </CategoryPreviewGrid>
            </AnimationChildrenComponent>
        </CategoryPreviewContainer>
    )
}

export default CategoryPreview;